/* Center the quiz content horizontally and vertically */
.quiz-container {
    display: flex;
    flex-direction: column; /* Stack title and quiz box vertically */
    justify-content: flex-start; /* Keep the content at the top */
    align-items: center; /* Center horizontally */
    height: 100vh; /* Full height of the viewport */
    background-color: #f7f7f7; /* Light background */
    width: 100%; /* Full width */
  }
  
  .quiz-box {
    width: 70%; /* Quiz container width */
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .quiz-title {
    text-align: center; /* Center the title text */
    margin-bottom: 20px;
    color: #333;
    font-size: 24px; /* Adjust font size */
    width: 70%; /* Ensure the title width matches the quiz box */
    margin: 0 auto; /* Center the title within the available space */
  }
  
  .question-container {
    margin-bottom: 20px;
  }
  
  .question-box {
    background-color: #f0f0f0; /* Slightly greyed background for the question */
    padding: 15px;
    border-radius: 8px;
    margin-bottom: 10px;
  }
  
  label {
    font-size: 16px;
    margin-right: 10px;
  }
  
  input[type="radio"] {
    margin-right: 10px;
  }
  
  span {
    font-size: 18px;
  }
  